<template>
  <div class="container">
    <el-card shadow="never">
      <div class="header">
        <div class="left">
          <el-form ref="form" :model="listObj" label-width="80px">
            <el-form-item label="标签名称">
              <el-input v-model="listObj.tagName"></el-input>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="listObj.state" placeholder="请选择">
                <el-option label="启用" value="1"></el-option>
                <el-option label="禁用" value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <el-button class="clear" @click="clear">清除</el-button>
          <el-button type="primary" @click="search">搜索</el-button>
        </div>
        <div class="right">
          <el-button type="success" icon="el-icon-edit" @click="addTag"
            >新增标签</el-button
          >
        </div>
      </div>
      <el-alert type="info" show-icon :closable="false"
        ><span slot="title">数据一共{{ total }}条</span></el-alert
      >
      <!-- 表格 -->
      <el-table
        :key="tableKey"
        :data="dataList"
        :row-class-name="rowClassStatus"
        v-loading="listLoading"
        element-loading-text="给我一点时间"
        fit
        highlight-current-row
        style="width: 100%"
      >
        <el-table-column type="index" width="50" label="序号">
        </el-table-column>
        <el-table-column align="center" label="所属学科">
          <template slot-scope="scope">
            <span>{{ scope.row.subjectName }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="标签名称">
          <template slot-scope="scope">
            <span>{{ scope.row.tagName }}</span>
          </template>
        </el-table-column>

        <el-table-column label="创建者">
          <template slot-scope="scope">
            <span>{{ scope.row.username }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="创建日期">
          <template slot-scope="scope">
            <span>{{
              scope.row.addDate | parseTimeByString('{y}-{m}-{d} {h}:{i}:{s}')
            }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="状态">
          <template slot-scope="scope">
            <span>{{ scope.row.state == 0 ? '已禁用' : '已启动' }}</span>
          </template>
        </el-table-column>
        <!-- 操作 -->
        <el-table-column
          class="operate"
          align="center"
          label="操作"
          class-name="small-padding fixed-width"
        >
          <template slot-scope="scope">
            <el-button
              style="width:0 "
              type="text"
              size="mini"
              @click="handleUpdate(scope.row.id, scope.row.state)"
              >{{ scope.row.state == 0 ? '启用' : '禁用' }}</el-button
            >
            <el-button
              style="width:0"
              v-if="scope.row.status != 'deleted'"
              size="mini"
              type="text"
              :disabled="scope.row.state == 0 ? false : true"
              @click="
                editTag(scope.row.id, scope.row.tagName, scope.row.subjectName)
              "
              >修改</el-button
            >
            <el-button
              style="width:0"
              v-if="scope.row.status != 'deleted'"
              size="mini"
              type="text"
              :disabled="scope.row.state == 0 ? false : true"
              @click="removeTag(scope.row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 修改目录对话框 -->
      <el-dialog
        title="修改目录"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose"
      >
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="所属学科" prop="subjname">
            <el-select
              v-model="ruleForm.subjname"
              placeholder=""
              style="width:278px"
            >
              <el-option
                v-for="(item, index) in options"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目录名称" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sureEdit">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 新增目录 -->
      <el-dialog
        title="新增目录"
        :visible.sync="dialogVisible1"
        width="30%"
        :before-close="handleClose"
      >
        <el-form
          :model="ruleForm1"
          :rules="rules2"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="所属学科" prop="subjname">
            <el-select
              v-model="ruleForm1.subjname"
              placeholder="请选择"
              style="width:278px"
            >
              <el-option
                v-for="(item, index) in options"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目录名称" prop="name">
            <el-input
              v-model="ruleForm1.name"
              placeholder="请输入目录名"
            ></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible1 = false">取 消</el-button>
          <el-button type="primary" @click="sureAdd">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="100"
        background
        :hide-on-single-page="false"
        layout="prev, pager, next, sizes, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { list, changeState, remove, update, add } from '@/api/hmmm/tags.js'
import { simple } from '@/api/hmmm/subjects.js'
export default {
  created () {
    this.getlist()
    this.getsimple()
  },
  data () {
    return {
      tableKey: 0,
      dataList: [],
      PermissionGroupsList: [], // 权限组加载
      total: null,
      dialogStatus: '',
      alertText: '',
      listLoading: false,
      rowClassStatus: '',
      // 分页
      currentPage: 1,
      listObj: { page: 1, pagesize: 10, state: null, tagName: '' },
      total: 0,
      // 修改对话框
      options: [
        {
          value: '',
          subjectID: ''
        }
      ],
      dialogVisible: false,
      ruleForm: {
        name: '',
        subjname: '',
        subjectName: ''
      },
      rules: {
        name: [{ required: true, message: '请输入学科名称', trigger: 'blur' }],
        subjname: [
          { required: true, message: '请输入标签名称', trigger: 'change' }
        ]
      },
      editobj: {
        id: '',
        subjectID: '',
        tagName: ''
      },
      // 新增对话框
      dialogVisible1: false,
      ruleForm1: {
        name: '',
        subjname: ''
      },
      rules2: {
        name: [{ required: true, message: '请输入学科名称', trigger: 'blur' }],
        subjname: [
          { required: true, message: '请输入标签名称', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    // 获取列表
    async getlist () {
      const res = await list(this.listObj)
      this.dataList = res.data.items
      this.ruleForm.name = res.data.items.tagName
      this.total = res.data.counts
    },
    // 简单列表
    async getsimple () {
      const res = await simple()
      this.options = res.data
    },
    // 分页
    handleSizeChange (val) {
      this.listObj.pagesize = val
      this.getlist(this.listObj)
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.listObj.page = val
      this.getlist(this.listObj)
    },
    // 修改状态
    async handleUpdate (id, state) {
      await changeState({ id, state: state == 0 ? 1 : 0 })
      this.$message({
        message: '操作成功',
        type: 'success'
      })
      this.getlist()
    },
    // 修改
    async editTag (id, tagname, subname) {
      this.dialogVisible = true
      this.ruleForm.name = tagname
      this.ruleForm.subjname = subname
      this.editobj.id = id
    },
    // 关闭对话框
    handleClose () {
      this.dialogVisible = false
      this.dialogVisible1 = false
      this.resct()
    },
    // 清空表单
    resct () {
      this.ruleForm1 = {}
    },
    // 点击确认关闭对话框
    async sureEdit () {
      this.editobj.subjectID = this.ruleForm.subjname
      this.editobj.tagName = this.ruleForm.name
      await update(this.editobj)
      this.getlist()
      this.dialogVisible = false
    },
    // 删除
    removeTag (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          remove({ id })
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getlist()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 添加标签
    addTag () {
      this.dialogVisible1 = true
    },
    async sureAdd () {
      var addobj = {
        subjectID: this.ruleForm1.subjname,
        tagName: this.ruleForm1.name
      }
      await add(addobj)
      this.resct()
      this.listObj.page = 1
      this.currentPage = 1
      this.getlist()
      this.dialogVisible1 = false
    },
    // 搜索
    search () {
      this.getlist()
    },
    // 清除
    clear () {
      this.listObj.tagName = ''
      this.listObj.state = ''
      this.listObj.state = null
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.container {
  padding: 10px;
  .el-card {
    .header {
      display: flex;
      justify-content: space-between;
      .left {
        display: flex;
        .el-form {
          display: flex;
        }
        .clear {
          margin-left: 10px;
        }
        .el-button {
          height: 36px;
        }
      }
    }
    .el-alert {
      margin-bottom: 25px;
    }
    .el-pagination {
      float: right;
      margin: 25px;
    }
    .el-button--mini,
    .el-button--small {
      font-size: 14px;
      margin: 10px;
      border-radius: 3px;
    }
    ::v-deep .el-dialog__footer {
      text-align: right;
    }
  }
}
</style>
